.banner {
  background-image: url(../../../../build/img/xbanner.webp);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}
.banner .title-banner {
  width: 60%;
  font-size: 1.7rem;
  margin: 2rem;
  color: red;
  text-align: center;
  background: rgba(70, 69, 69, 0.34);
  padding: 0.5rem;
}
@media (min-width: 768px) {
  .banner .title-banner {
    font-weight: bolder;
  }
}
