  display: flex;
  flex-flow: row wrap;
  width: 50%;
  justify-content: center;
}
.banner .content-btn-banner button {
  margin: 1rem;
}

.packages {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  width: 100%;
  margin-top: 1.5rem;
}
.packages .title-packages {
  color: #0054ff;
  font-size: 1rem;
  letter-spacing: 3px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 2rem;
  margin-top: 4rem;
  width: 90%;
}
@media (min-width: 768px) {
  .packages .title-packages {
    width: 23%;
  }
}
.packages .content-pictures-packages {
  display: flex;
  flex-flow: row wrap;
  width: 80%;
  justify-content: space-evenly;
}
.packages .content-pack {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  width: 80%;
  justify-content: center;
}
@media (min-width: 768px) {
  .packages .content-pack {
    flex-flow: row wrap;
  }
}
.packages .content-pack .package {
  display: flex;
  flex-flow: column wrap;
  width: 90%;
  margin: 1rem;
  align-items: flex-start;
}
.packages .content-pack .package .img-packs {
  align-self: center;
  margin-bottom: 0.5rem;
}
@media (min-width: 768px) {
  .packages .content-pack .package {
    width: 30%;
  }
}
.packages .content-pack .package .description.package {
  text-align: justify !important;
}
@media (min-width: 768px) {
  .packages .content-pack .package {
    margin: 2rem;
  }
}
.packages .content-pack .package .title-package {
  font-size: 1.6rem;
  font-weight: bolder;
  color: 900;
  text-align: center;
}
.packages .content-pack .package #fix-pack {
  margin-top: 1rem !important;
}
.packages .content-pack .package .pack {
  display: flex;
  flex-flow: column wrap;
  border: 1px solid #ccc;
  padding: 0.5rem;
  width: 100%;
  margin-bottom: 1rem;
}
.packages .content-pack .package .pack .content-service {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
.packages .content-pack .package .pack .content-service .title-pack {
  text-transform: uppercase;
  font-size: 1rem;
}
.packages .content-pack .package .pack .desc {
  display: flex;
  flex-flow: row wrap;
}
.packages .content-pack .package .pack .desc * {
  margin: 0.5rem 0.5rem 0.5rem 0;
  font-size: 0.9rem;
}

.title-package_fix {
  color: 900;
  font-size: 1.6rem;
  margin: 1.5rem 0 1.2rem 0;
  font-weight: bolder;
  letter-spacing: 1px;
